/* CSS Fading effects */
/*https://fvsch.com/transition-fade/test5.html#test3*/
.visible {
   display: "block";
   visibility: visible;
   opacity: 1;
   -webkit-transition: opacity 1.5s linear;
   transition: opacity 1.5s linear;
 }

 .visible-t {
  display: "block";
  visibility: visible;
  opacity: 1;
  -webkit-transition: opacity 0.5s ease-in;
  transition: opacity 0.5s ease-in;
}

.hidden {
   opacity: 0;
   visibility: hidden;
   -webkit-transition: opacity 1s ease-in;
   transition: opacity 1s ease-in;
 }

 .hidden-t {
  opacity: 0;
  visibility: hidden;
  -webkit-transition: opacity 1s ease-in;
  transition: opacity 1s ease-in;
  display: none;
}


